import { Injectable } from '@angular/core';
import { ToastrService } from 'ngx-toastr';

@Injectable({
  providedIn: 'root',
})
export class UtilService {
  constructor(private toastr: ToastrService) {}

  //
  // FORMATTING
  //

  formatDate(d, options?) {
    if (!options) {
      options = {
        hour12: false,
        timeZoneName: 'short',
      };
    }
    const date = new Date(d);
    return `${date.toLocaleString('en-us', options)}`;
  }

  formatSeconds(seconds) {
    return new Date(seconds * 1000).toISOString().substr(11, 8);
  }

  objToSeconds(obj) {
    return obj.hour * 60 * 60 + obj.minute * 60 + obj.second;
  }

  objToHms(obj) {
    let dispArr = [];
    let hDisplay = obj.hour > 0 ? obj.hour + (obj.hour == 1 ? 'h' : 'hrs') : '';
    if (hDisplay) dispArr.push(hDisplay);
    let mDisplay =
      obj.minute > 0 ? obj.minute + (obj.minute == 1 ? 'm' : 'mins') : '';
    if (mDisplay) dispArr.push(mDisplay);
    let sDisplay = obj.second > 0 ? obj.second + 's' : '';
    if (sDisplay) dispArr.push(sDisplay);
    let resStr = dispArr.length > 1 ? dispArr.join(', ') : dispArr.join('');
    return resStr === '' ? 0 : resStr;
  }

  //
  // TOAST MESSAGES
  //

  msgOptions = {
    positionClass: 'toast-top-center',
    closeButton: true,
    timeOut: 3000,
  };

  incompleteFieldsMsg() {
    this.toastr.warning('', 'Please fill out all fields', this.msgOptions);
  }

  submitSuccessMsg() {
    this.toastr.success('', 'Submitted', this.msgOptions);
  }

  lookupAddMsg() {
    this.toastr.success('', 'Lookup Added!', this.msgOptions);
  }

  submitErrorMsg() {
    this.toastr.error(
      'Please try again later',
      'Submission Error',
      this.msgOptions,
    );
  }

  gteLteErrorMsg() {
    this.toastr.warning(
      'Lower limit cannot be greater than upper limit',
      'Invalid input',
      this.msgOptions,
    );
  }

  emptyNewLookupMsg() {
    this.toastr.warning(
      'Cannot submit empty lookup',
      'Invalid Input',
      this.msgOptions,
    );
  }

  deleteErrorLookupMsg() {
    this.toastr.error(
      'Please try again later',
      'Unable to delete lookup',
      this.msgOptions,
    );
  }

  lookupValueTransferMsg(count) {
    this.toastr.success(
      ``,
      `${count} entries have been modified.`,
      this.msgOptions,
    );
  }

  lookupValueDeleteMsg(count) {
    let e = count > 1 ? 'entries have' : 'entry has';
    this.toastr.success(``, `${count} ${e} been deleted.`, this.msgOptions);
  }
}
