import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { map, first } from 'rxjs/operators';
import { AuthService } from '../auth/auth.service';
import { Entry, Note } from 'src/app/models/entry';
import { User } from 'src/app/models/user';
import { Lookup, LookupValue } from 'src/app/models/lookup';
import { RawQuery } from 'src/app/models/rawQuery';
import { SearchSub } from 'src/app/models/searchSub';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class DataService {
  constructor(private http: HttpClient) {}

  //
  // ENTRIES
  //

  createEntry(
    submittedBy: string,
    date: Date,
    location: LookupValue,
    show: LookupValue,
    classification: LookupValue,
    heardOnAir: LookupValue,
    actionTaken: LookupValue,
    onAir: boolean,
    funder: boolean,
    duration: number,
    eTag: string,
    entry: string,
    createItServiceTicket: boolean,
    isDeleted: boolean,
  ) {
    return this.http
      .post<any>(`api/db/entry`, {
        submittedBy,
        date,
        location,
        show,
        classification,
        heardOnAir,
        actionTaken,
        onAir,
        funder,
        duration,
        eTag,
        entry,
        createItServiceTicket,
        isDeleted,
      })
      .pipe(
        map(response => {
          return response as Entry;
        }),
      );
  }

  uploadAttachment(entryId, f: FormData) {
    return this.http.post<any>(`api/db/entry/${entryId}/attachment`, f).pipe(
      map(response => {
        return response;
      }),
    );
  }

  downloadAttachment(entryId): Observable<any> {
    return this.http
      .get(`api/db/entry/${entryId}/attachment`, { responseType: 'blob' })
      .pipe(
        map(response => {
          return response;
        }),
      );
  }

  addNoteToEntry(entryId, author, content) {
    return this.http
      .put(`api/db/entry/${entryId}/note`, {
        author,
        content,
      })
      .pipe(
        map(response => {
          return response as Note[];
        }),
      );
  }

  deleteNoteFromEntry(entryId, noteId) {
    return this.http.delete(`api/db/entry/${entryId}/note/${noteId}`).pipe(
      map(response => {
        return response as Note[];
      }),
    );
  }

  getUniqueSubmitters() {
    return this.http.get('api/db/entry/unique-submitters').pipe(
      map(response => {
        return response as String[];
      }),
    );
  }

  deleteEntry(entry) {
    return this.http.delete(`api/db/entry/${entry._id}`).pipe(
      map(response => {
        return response as Entry;
      }),
    );
  }

  flagEntryAsDeleted(entry) {
    return this.http.put(`api/db/entry/${entry._id}/flag`, {}).pipe(
      map(response => {
        return response as Entry;
      }),
    );
  }

  //
  // LOOKUP VALUES
  //

  getLookupValues() {
    return this.http.get(`api/db/lookup-value`).pipe(
      map(response => {
        return response as LookupValue[];
      }),
    );
  }

  addLookupValue(newValue: LookupValue) {
    return this.http.post(`api/db/lookup-value`, newValue).pipe(
      map(response => {
        return response as LookupValue;
      }),
    );
  }

  updateLookupValue(updatedVal: LookupValue) {
    return this.http
      .put(`api/db/lookup-value/${updatedVal._id}`, updatedVal)
      .pipe(
        map(response => {
          return response as LookupValue;
        }),
      );
  }

  deleteLookupValue(valId) {
    return this.http.delete(`api/db/lookup-value/${valId}`).pipe(
      map(response => {
        return response as LookupValue;
      }),
    );
  }

  //
  // USER
  //

  // Return new user or existing user with tag
  createUser(user) {
    return this.http.post(`api/db/user`, user).pipe(
      map(response => {
        return response as User;
      }),
    );
  }

  getUser(personId) {
    return this.http.get(`api/db/user/${personId}`).pipe(
      map(response => {
        return response as User;
      }),
    );
  }

  updateUser(user) {
    return this.http.put(`api/db/user/${user._id}`, user).pipe(
      map(response => {
        return response as User;
      }),
    );
  }

  subToSearch(personId, search) {
    return this.http.post(`api/db/user/${personId}/subbed-search`, search).pipe(
      map(response => {
        return response as SearchSub[];
      }),
    );
  }

  deleteSearch(personId, searchId) {
    return this.http
      .delete(`api/db/user/${personId}/subbed-search/${searchId}`)
      .pipe(
        map(response => {
          return response as SearchSub;
        }),
      );
  }
}
