import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { forkJoin, Observable } from 'rxjs'; // RxJS 6 syntax

@Injectable({
  providedIn: 'root',
})
export class UserService {
  constructor(private http: HttpClient) {}

  // Gets user information from log in
  getUser() {
    return this.http.get<any>(`api/user`).pipe(
      map(user => {
        return user;
      }),
    );
  }

  // Gets extensive user information from api v1
  getPersonInfo() {
    return this.http.get<any>('api/user/info').pipe(
      map(response => {
        return response;
      }),
    );
  }

  getAllInfo(): Observable<any[]> {
    let userInfo = this.http.get('api/user');
    let personInfo = this.http.get('api/user/info');
    return forkJoin([userInfo, personInfo]);
  }
}
