import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../services/auth/auth.service';
import { first } from 'rxjs/operators';
import { UserService } from '../../services/user/user.service';
import { DataService } from 'src/app/services/data/data.service';
import { RawQuery } from 'src/app/models/rawQuery';
import { ToastrService } from 'ngx-toastr';
import { SearchSub } from 'src/app/models/searchSub';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss'],
})
export class ProfileComponent implements OnInit {
  personId: number;
  name: string;
  email: string;
  subscriptions: SearchSub[];

  constructor(
    private userService: UserService,
    private dataService: DataService,
    private toastr: ToastrService,
  ) {}

  ngOnInit() {
    this.userService // Get current user from cookie payload
      .getUser()
      .pipe(first())
      .subscribe(user => {
        this.personId = user.personId;
        this.dataService
          .getUser(user.personId) // Use cookie payload info to get full user info from db
          .pipe(first())
          .subscribe(user => {
            this.name = user.info.person.displayName;
            this.email = user.info.person.email;
            this.subscriptions = user.subbedSearches;
          });
      });
  }

  unSub(id) {
    this.dataService
      .deleteSearch(this.personId, id)
      .pipe(first())
      .subscribe(sub => {
        this.subscriptions = this.subscriptions.filter(
          ({ _id }) => sub._id !== _id,
        );
        this.toastr.success(
          '',
          `
        "${sub.name}" has been removed from your subscriptions.`,
          {
            positionClass: 'toast-top-center',
            closeButton: true,
            timeOut: 3000,
          },
        );
      });
  }
}
