import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { RawQuery } from 'src/app/models/rawQuery';
import { SearchSub } from 'src/app/models/searchSub';

@Component({
  selector: 'app-subscription',
  templateUrl: './subscription.component.html',
  styleUrls: ['./subscription.component.scss'],
})
export class SubscriptionComponent implements OnInit {
  @Input() subbedSearch: SearchSub;
  @Output() onDelete = new EventEmitter();

  props = [];

  constructor() {}

  ngOnInit() {
    console.log(this.subbedSearch);
    Object.keys(this.subbedSearch.queryInfo).forEach(key => {
      let val = this.subbedSearch.queryInfo[key];
      this.props.push({
        key,
        val:
          (val !== null && val.length) || typeof val === 'boolean'
            ? val
            : 'Any',
      });
    });
  }

  delete() {
    this.onDelete.emit(this.subbedSearch._id);
  }
}
