import { RawQuery } from './rawQuery';
import { Entry } from './entry';

export class SearchSub {
  constructor(name, queryInfo, interval) {
    this.name = name;
    this.queryInfo = queryInfo;
    this.interval = interval;
  }
  _id: any;
  name: string;
  queryInfo: RawQuery;
  interval: {
    title: string;
    val: number;
  };
  queue: Entry[];
}
