import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { first } from 'rxjs/operators';
import { AuthService } from '../../services/auth/auth.service';
import { ToastrService } from 'ngx-toastr';
import { DataService } from 'src/app/services/data/data.service';
import { UserService } from 'src/app/services/user/user.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
})
export class LoginComponent implements OnInit {
  loginForm: FormGroup;
  isLoading: boolean = false;

  constructor(
    private dataService: DataService,
    private userService: UserService,
    private formBuilder: FormBuilder,
    private router: Router,
    private authService: AuthService,
    private toastr: ToastrService,
  ) {}

  ngOnInit() {
    this.loginForm = this.formBuilder.group({
      username: ['', Validators.required],
      password: ['', Validators.required],
    });
  }

  // convenience getter for easy access to form fields
  get form() {
    return this.loginForm.controls;
  }

  onSubmit() {
    console.log(this.loginForm.value);
    if (this.loginForm.status == 'VALID') {
      this.isLoading = true;
      let encodedPassword = btoa(this.form.password.value);
      this.authService
        .login(this.form.username.value, encodedPassword)
        .pipe(first())
        .subscribe(
          ({ user }) => {
            console.log(user);
            this.authService.isLoggedIn = true;
            this.createUser(user).then(user => {
              console.log(user);
              this.isLoading = false;
              this.router.navigate(['/']);
              this.toastr.success('', 'Login Success', {
                positionClass: 'toast-top-center',
                closeButton: true,
                timeOut: 3000,
              });
            });
          },
          err => {
            console.log(err);
            this.isLoading = false;
            this.toastr.error(`${err.error.info}`, 'Login Failure', {
              positionClass: 'toast-top-center',
              closeButton: true,
              timeOut: 3000,
            });
          },
        );
    } else {
      this.toastr.warning('Please fill out all fields', 'Login Failure', {
        positionClass: 'toast-top-center',
        closeButton: true,
        timeOut: 3000,
      });
    }
  }

  createUser(user) {
    let promise = new Promise((resolve, reject) => {
      this.dataService
        .createUser(user)
        .pipe(first())
        .subscribe(
          user => {
            this.userService
              .getPersonInfo()
              .pipe(first())
              .subscribe(
                userInfo => {
                  this.dataService
                    .updateUser({ ...user, info: userInfo })
                    .pipe(first())
                    .subscribe(
                      user => {
                        resolve(user);
                      },
                      err => {
                        reject();
                      },
                    );
                },
                err => {
                  reject();
                },
              );
          },
          err => {
            reject();
          },
        );
    });
    return promise;
  }
}
