import { Injectable } from '@angular/core';
import { Entry, Note } from 'src/app/models/entry';
import { HttpClient } from '@angular/common/http';
import { Lookup, LookupValue } from 'src/app/models/lookup';
import { filter, repeatWhen } from 'rxjs/operators';
import { RawQuery } from 'src/app/models/rawQuery';
import { UtilService } from '../util/util.service';
import { resolve } from 'url';

@Injectable({
  providedIn: 'root',
})
export class SearchService {
  hasInit: boolean = false;

  constructor(private http: HttpClient, private utilService: UtilService) {}

  searchEntries(query) {
    let promise = new Promise<any>((resolve, reject) => {
      this.http
        .post<any>(`api/search`, {
          query,
        })
        .toPromise()
        .then(
          data => {
            console.log(data);
            this.hasInit = true;
            resolve(data);
          },
          err => {
            console.log(err);
            reject(err);
          },
        );
    });
    return promise;
  }

  //Serialize search data into query
  buildQuery(rawQuery: RawQuery) {
    const {
      from,
      size,
      fromDate,
      toDate,
      techNames,
      location,
      show,
      classification,
      heardOnAir,
      actionTaken,
      onAir,
      duration,
      funder,
      eTag,
      entry,
    } = rawQuery;

    // Build query object
    let promise = new Promise<any>((resolve, reject) => {
      let mustArr = [];
      let filterArr = [];

      // Never pull entries flagged as deleted
      filterArr.push({ term: { isDeleted: false } });

      if (entry) mustArr.push({ match: { entry } });
      if (eTag) mustArr.push({ match: { eTag } });

      if (techNames.length)
        filterArr.push({ terms: { submittedBy: techNames } });
      if (location.length) filterArr.push({ terms: { location } });
      if (show.length) filterArr.push({ terms: { show } });
      if (classification.length) filterArr.push({ terms: { classification } });
      if (heardOnAir.length) filterArr.push({ terms: { heardOnAir } });
      if (actionTaken.length) filterArr.push({ terms: { actionTaken } });
      if (fromDate && toDate) {
        filterArr.push({
          range: {
            date: {
              gte: fromDate,
              lte: toDate,
            },
          },
        });
      } else if (fromDate && !toDate) {
        filterArr.push({ term: { date: fromDate } });
      }
      if (duration) {
        filterArr.push({
          range: {
            duration: {
              gte: duration.gte,
              lte: duration.lte,
            },
          },
        });
      }
      if (onAir != undefined) filterArr.push({ term: { onAir } });
      if (funder != undefined) filterArr.push({ term: { funder } });

      const query = {
        from: from,
        size: size,
        query: {
          bool: {
            must: mustArr,
            filter: filterArr,
          },
        },
      };
      resolve(query);
    });
    return promise;
  }

  outputToCsv(query) {
    let promise = new Promise<any>((resolve, reject) => {
      query.from = undefined;
      query.size = undefined;
      this.searchEntries(query).then(({ results }) => {
        let output = [];
        const keys = Object.keys(results[0]);
        output.push(keys);

        results.forEach(result => {
          let row = [];
          keys.forEach(key => {
            if (key === 'date' || key === 'createdAt') {
              row.push(
                this.utilService.formatDate(result[key]).replace(',', ''),
              );
            } else if (key === 'notes') {
              row.push(
                result[key]
                  .map(
                    note =>
                      `${note.author} at ${this.utilService
                        .formatDate(note.createdAt)
                        .replace(',', '')}: ${note.content}`,
                  )
                  .join(' // '),
              );
            } else row.push(result[key]);
          });
          output.push(row);
        });

        let csvContent =
          'data:text/csv;charset=utf-8,' +
          output.map(e => e.join(',')).join('\n');
        var encodedUri = encodeURI(csvContent);
        window.open(encodedUri);
        resolve();
      });
    });
    return promise;
  }
}
